.popup-edit__header {
  display: flex;
  justify-content: space-between;
}

.popup-edit__row {
  display: flex;
  align-items: center;
}

.popup-edit__print-doc {
  margin-right: 25px;
  align-self: center;
}

.popup-edit__attached-file {
  width: 100%;
  display: flex;
  justify-content: flex-end;
}

.popup-edit__upload-file {
  text-align: right;
  align-self: flex-end;
  margin-bottom: 10px;
}

.popup-edit__actions {
  margin-top: 20px !important;
  display: flex !important;
  justify-content: space-between !important;
}

.popup-edit__open-tasks {
  margin-top: 20px;
  font-size: 14px;
}

.MuiButton-outlinedDefault:hover {
  background-color: #bababb !important;
  color: white !important;
}
.MuiButton-outlinedPrimary:hover {
  background-color: #0551c7 !important;
  color: white !important;
}
.MuiButton-outlinedOk {
  border-color: #57f500 !important;
  color: #57f500 !important;
}
.MuiButton-outlinedOk:hover {
  border-color: #57f500 !important;
  background-color: #57f500 !important;
  color: white !important;
}

.popup-edit__row .MuiInput-input {
  font-size: 14px !important;
  font-family: var(--font-main);
}

.popup-edit__row > div:first-child {
  width: 200px;
  font-size: 14px;
}